@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.tabla{
    margin: 0;
    padding: 0;

    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 1rem;
    }

    .seleccion{
        border: 1px solid c.$light-blue-500;
        border-radius: 0 0 3rem;
        margin: 1rem 0;

        .cajaImagen{
            text-align: center;
            padding-bottom: 1rem;
            img{
                height: 17rem;
            }
        }
        h4{
            color: c.$blanco;
            background-color: c.$light-blue-500;
            padding: 1rem 0 1rem 1rem;
            margin: 0 0 1rem 0;
        }
    }

}