@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;



.productoSeleccionado {
    border: solid 1px darken(c.$light-blue-500, 10);
    margin-top: 1rem;

    .tituloProducto {
        text-align: start;
        margin-left: 3rem;
    }

    hr {
        border: solid 1px darken(c.$light-blue-500, 10);
    }

    .producto {
        display: block;
        margin-top: 1rem;
        padding: 1rem;

        .contenido {
            display:block;
            margin-top: 1rem;


            @include m.tablet {
                display: flex;
            }

            video {
                max-width: 100%;
            }

            .datosProducto {
                display: block;
                margin: 1rem;
                padding: 1rem;

                span {
                    color: blue;
                    margin-bottom: 2rem;
                }
                .descripcionProducto {
                    height: 10rem;
                    width: 100%;

                }
            }

        }

    }

    .opciones {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        flex: 1;
    
        .opcion {
            border: solid 1px darken(c.$light-blue-500, 10);
            border-radius: 1rem;
            margin: 1rem;
            padding: 1rem;
        
         
            .datos {
                display: flex;
                
                span {
                    margin: 1rem;
                }
            }
            
            .imag {
                display: flex;
                flex-wrap: wrap;

                img {
                    width: 10rem;
                }
            }

        }

    }

}


    /// MODAL 
    figure {
        display: inline-block;
        height: 100px;
        margin: 25px;
    }
    figcaption {
        font-style: oblique;
        text-align: center;
        margin: 10px 0px;
        color: hsl(0, 0%, 67%);
    }
    .imagen img {
        max-width: 100%;
        cursor: pointer;
    }
    .modalImg {
        cursor: pointer;
        border-radius: 0.4rem;
        max-height: 200px;
    }
    .imodalImg:hover {
        opacity: 0.8;
    }
    .modal {
        display: none;
        /* ocultar por defecto */
        position: fixed;
        width: 100%;
        height: 50%;
        position: fixed;
        top: 25%;
        left: 0;
        border: solid 1px;
        background: rgba(0, 0, 0, 0.60);
        //color: rgba(255, 255, 255, 0.87);
        padding-top: 5%;
        overflow: hidden;
    }
    #modalvid {
        display: block;
        position: fixed;
        width: 90%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);   
    }
    #modalImg {
        display: block;
        position: fixed;
        width: auto;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);   
    }
    #modalClose {
        position: absolute;
        cursor: pointer;
        top: 30px;
        right: 50px;
        color: #fff;
        transition: all;
        font-weight: bold;
        font-size: 5rem;
    }
    #modalText {
        margin: auto;
        display: block;
        text-align: center;
        color: #ccc;
    }
    /* animaciones */
    #modalText {
        animation-name: zoom;
        animation-duration: 0.5s;
    }
    
    @keyframes zoom {
        from {
            transform: scale(0)
        }
        to {
            transform: scale(1)
        }
    }