h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 1.5rem 0;
  font-weight: 900;
  color: rgb(33, 192, 192);
  text-align: center;
}

h1 {
  font-size: calc(1em + 2vw);
}

h2 {
  font-size: calc(0.9em + 1.5vw);
}

h3 {
  font-size: calc(0.8em + 1vw);
  margin: 0;
}

h4 {
  font-size: calc(0.5em + 0.8vw);
  margin: 0;
  padding: 0;
}

h5 {
  font-size: calc(0.5em + 0.8vw);
  font-family: "Poppins", sans-serif;
  color: rgb(33, 192, 192);
  margin: 0;
  padding: 0;
}

p {
  color: #1a1b15;
  font-size: calc(0.4em + 0.8vw);
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: #FFFFFF;
  color: rgb(33, 192, 192);
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  position: relative;
  min-height: 100vh;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

footer {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  position: absolute;
}

hr {
  border: 1px solid rgb(230.25, 231.375, 224.625);
  width: 95%;
}

/** Media Queries **/
.mostrar {
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
  height: auto !important;
}

.ocultar {
  display: none !important;
}

.sombraCaja {
  -webkit-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  -moz-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
}

.sombraTarjetas {
  box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
  -webkit-box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
  -moz-box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
}

.contenido_centrado {
