  padding: 1rem;
}

.peque {
  width: 7rem !important;
  height: 7rem !important;
}

.boton_verde {
  background-color: rgb(33, 192, 192);
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  font-size: calc(0.6em + 0.7vw);
  text-align: center;
  margin: 0.2rem 0.5rem 0.2rem 0;
  padding: 1rem 1rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
