
// MAVEGACION SUPERIOR

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.header{
    background-color: c.$principal;

    .barra{
        display: flex;
        align-items: center;
        width: 100%;

        .logo {
            width: 10rem;

            img{
                width: 8rem;
            }
        }

        .mobile_menu {
            margin: 0 0 0 2rem;

            @include m.tablet{
                display: none;
            }
        
            img{
                width: 2.5rem;
            }
        }

        .navegacion{
            visibility: hidden;
            opacity: 0;
            height: auto;
            transition: visibility .2s ease-in-out, opacity 0.5s linear, height 0.5s linear;
            text-align: center;
            margin-left: 4rem;

            @include m.tablet{
                display: flex;
                justify-content: flex-end;
                visibility: visible;
                opacity: 1;
                margin-top: 1rem;
                width: 90%;
                
            }

            a{
                color: c.$blanco;
                text-decoration: none;
                font-size: 1.8rem;
                display: block;
                text-align: center;
                padding: 0.3rem;
                color: c.$blanco;
                

                @include m.tablet{
                    margin-right: 2rem;
                    font-size: 2rem;
                    font-weight: t.$bold;

                }
                
                
            }

            .dark_mode_boton{
                width: 2.5rem;
                filter: invert(100%);
                cursor: pointer;

                @include m.tablet{
                    height: 2rem;
                }
            }


        }
    }
}