@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


// PRODUCTOS

.listado_productos {

    .contenedor_tabla {
        tbody{
            img {
                width: 10rem;
            }
        }
    }

    .P_ver,
    .P_actualizar,
    .P_eliminar {
        max-width: 3.5rem;
    }

}

.crear_producto,
.actualizar_productos {

    .select_categoria {
        width: 70%;
        border: none;
    }

    .video {
        max-width: 20rem;
        margin: 1rem;
    }

    .descripcion,
    .enlaceProducto {
        width: 100%;
        border: none;
    }

    label {
        font-size: 1.2rem;
    }

    .imagenesActuaOpciones {
        display: flex;
        flex-wrap: wrap;

        .img1,
        .img2,
        .img3 {
            width: 15rem;
        } 
    }
    .contenedor_imagen {
        img {
            width: 10rem;
        }
    }

    .fechas {
        display: flex;
        width: 100%;
        justify-content: space-around;

        .campo {
            display: block;
            padding: 0.5rem;
            margin: 1rem 0.2rem 0 0;
            width: 50%;

            input {
                font-size: 1.5rem;
            }
        }
    }


    
    .opciones {
        margin: 1rem 0  0 0;
        padding: 0rem 1rem 0 1rem;
        border-radius: 1rem;
        border: solid 1px darken(c.$gris, 10);

        .zona_arriba {
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin-top: 1rem;
            border-radius: 1rem;
            border: solid 1px darken(c.$light-blue-500, 10);
            background-color: c.$light-blue-200;

            label {
                font-size: 2rem;
                font-weight: t.$bold;
                color: c.$blanco;
            }

        }

        .zona_abajo {
            width: 100%;
            margin-top: 1rem;


            .conOpciones {
                margin-top: 1rem;
                border-radius: 1rem;
                border: solid 1px darken(c.$light-blue-500, 10);
                padding: 1rem;
                margin-bottom: 1rem;

                .divCabecera {
                    width: 100%;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;

                    .camp {
                        margin: 1 1rem  1 0;
                        padding: 0.2rem;
                        border-radius: 1rem;

                        border: solid 1px darken(c.$gris, 10);
                        display: flex;
                        align-items: center;
                        flex: 1;
                        
                        label {
                            width: 7rem;
                            flex: 1;
                        }
                        input{
                            width: 100%;
                            border: none;
                            border-radius: 10px;
                            flex: 1;
                            padding: 1rem;
                        }

                        select {
                            border: none;
                        }
                    }

                }

                .centro {
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;
                    margin: 1rem 0 1rem 0;

                    textarea{
                        margin: 0.5rem;
                        
                    }

                    img {
                        width: 5rem;
                        height: 5rem;
                        margin: 0 1rem 0 1rem;
                    }
                }

                .divImagenesBotones {
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;

                    .divImagenes {
                        display: flex;
                        justify-content: flex-start;
                        align-items: flex-start;
                        width: 50%;
                        
                        .divImagenesActuaOpciones {
                            display: flex;
                            flex-direction: column;

                            width: 100%;
                            height: 15rem;
                            margin: 0 0 0 1rem;
                            //padding: 0.5rem;
    
                            div {
                                width: 33%;
                            }
                            .img1 {
                                width: 100%;
                                margin: 0.5rem 0 ;
                                flex: 1;
                            }
                        }
                    }
    
                    .botonesOp {
                        display: flex;
                        justify-content: flex-end;
                        align-items: flex-end;
                        margin-bottom: 1rem;
                        width: 50%;
    
            
                    }
                }

  
            }

        }


    }
    .contenedor_botones {
        margin-top: 5rem;
    }
    
}



// Clientes
.productos_clientes {
    .nombre_pagina{
        margin-top: 1rem;
        font-size: calc(0.7em + 1vw);
    }

    
    .imagen_productos {
        display: block;

        .imagen1,
        .imagen2,
        .imagen3,
        .imagen4 {
            width: 7rem;
            height: 7rem;
        }
    }
    .video_producto {
        width: 7rem;
        height: 7rem;
    }
    .texto {
        padding: 1rem;
    }

    .modal {
        display: none;
    }
}