// Utilidades

@use 'variables' as v;
@use 'mixins' as m;
@use 'colores' as c;


// Mostrar/Ocultar secciones
.mostrar{
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
    height: auto !important;
}

.ocultar{
    display: none !important;
}

// Sombras
.sombraCaja{
    -webkit-box-shadow: 10px 10px 5px -6px rgba(158,156,158,1);
    -moz-box-shadow: 10px 10px 5px -6px rgba(158,156,158,1);
    box-shadow: 10px 10px 5px -6px rgba(158,156,158,1);
}

.sombraTarjetas {
    box-shadow: 0px 0px 10px -2px rgba(148,139,139,0.76);
    -webkit-box-shadow: 0px 0px 10px -2px rgba(148,139,139,0.76);
    -moz-box-shadow: 0px 0px 10px -2px rgba(148,139,139,0.76);
}

.contenido_centrado {
    max-width: 100rem;
    text-align: center;
}

// Alineacion
.alinear_derecha{
    display: flex;
    justify-content: flex-end;
}

.alinear_izquierda{
    display: flex;
    justify-content: flex-start;
}

// Ancho
.w-100{
    width: 100%;
}

// Cambia tamaño imagen
.grande{
    width: 34rem !important;
    height: 34rem !important;
    padding: 1rem;
}
.peque{
    width: 7rem !important;
    height: 7rem !important;
}