@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

:root{
  --width_cuadra: 100px;
  --heigh_cuadra: auto;
  --padding: 0,3rem

}


 @media (max-width: 1500px){
      :root{
        --width_cuadra: 200px;
      }
    }

.meteo {

    .contenedor_meteo {

        .conte_uno,
        .conte_dos,
        .conte_tres {
            display: flex;
            justify-content: space-between;
            align-items: center;

            padding: 0.5rem;
        }

        .conte_linea {
            display: flex;
            justify-content: center;
            
            hr {
                margin: 0.5rem 0;
            }
        }

        .conte_uno {

            h5 {
                text-transform: uppercase;
            }
            .parra {
                display: flex;
            }
        }

        .conte_dos {

            .izquierda,
            .derecha {
            
                .contenido {
                    display: flex;
                }
            }

            .centro {
                img {
                    max-width: 10rem;
                }
            }
            .unoI,
            .dosI,
            .tresI,
            .unoD,
            .dosD {
                display: flex;
                justify-content: space-between;
                align-items: center;

                margin: 0.3rem 0;

                img {
                    max-width: 2.3rem;
                    padding-right: 0.5rem;
                }
            }

        }
        
        .conte_tres {
            margin: 0 1rem;
            padding: 0;

            .viento {
                display: flex;
                justify-content: center;
                margin-top: 0.25rem;

                h5 {
                    padding-right: 0.5rem;
                }
                p {
                    padding-right: 0.2rem;
                }
            }
        }

        .conte_aire {
            margin: 1rem 0; 

            .calidad {
                margin: 0.25rem 1rem ;
                display: flex;
                align-items: center;
                justify-content: space-between;
        
                .izd {
                    display: flex;
                    h5 {
                        margin: 0;
                        padding-right: 0.5rem;
                    }
        
                    .ica {
                        padding: 0.2rem 1rem;
        
                        border-radius: 0.5rem;
        
                        text-align: center;
                        font-weight: 600;
                        color: c.$blanco;
                    }
                }

            }

            .contenido_aire {
                margin-right: 0.5rem;
            
                display: grid;
                grid-template-rows: 2;
                grid-template-columns: repeat(4, 2fr);

                @include m.tablet {
                    grid-template-rows: 1;
                    grid-template-columns: repeat(8, 2fr);
                }
                .c_uno,
                .c_dos,
                .c_tres,
                .c_cuatro {
                    display: flex;
                    align-items: center;

                    border: 1px solid lighten(c.$negro, 80%);
                    border-radius: 0.5rem;

                    padding: 0.2rem;
                    margin: 0.2rem;

                    h5 {
                        padding-right: 0.5rem;
                    }
                    p {
                        padding: 0.3rem;
                    }
                }
            }


            .conte_leyenda {
                margin: 0 1rem 1rem 1rem;
                padding: 1rem;
                
                border: 1px solid lighten(c.$blue-grey-200, 10%);
                border-radius: 0.5rem;

                display: flex;
                justify-content: space-around;

                p {
                    margin-bottom: 0.3rem;
                }
            }
        }
        
/*  ***************** Indice IV ****************** */
        .conte_cuatro {
            .cuerpo-iv{
                display: flex;
                font-size: calc(0.4em + .5vw);
                margin: 0;
                padding: 0;
                margin-top: 1rem;
            }

            .cuadrado0, .cuadrado1, .cuadrado2{
                text-align: center;
                padding-top: 0.1rem;
                padding: var(--padding);
                width: var(--width_cuadra); 
                height: var(--heigh_cuadra); 
                background: #8FD51E ;
            }

            .cuadrado3, .cuadrado4, .cuadrado5{
                text-align: center;
                padding: var(--padding);
                width: var(--width_cuadra); 
                height: var(--heigh_cuadra); 
                background: #F4F656  ;
            }

            .cuadrado6, .cuadrado7{
                text-align: center;
                padding-top: 0.1rem;
                padding: var(--padding);
                width: var(--width_cuadra); 
                height: var(--heigh_cuadra); 
                background: #F9E51C ;
            }

            .cuadrado8, .cuadrado9, .cuadrado10{
                text-align: center;
                padding-top: 0.1rem;
                padding: var(--padding);
                width: var(--width_cuadra); 
                height: var(--heigh_cuadra); 
                background: #F98E1C ;
            }

            .cuadrado11{
                text-align: center;
                padding-top: 0.1rem;
                padding: var(--padding);
                width: var(--width_cuadra); 
                height: var(--heigh_cuadra); 
                background: #F9411C ;
            }

        }

        .conte_cinco {
            display: flex;
            justify-content: space-between;
            padding: 0.5rem;
            margin-top: 1rem;

            .horaamanece,
            .horaanochece {
                display: flex;
                justify-content: flex-start;
                
                h5 {
                    padding-right: 0.5rem;
                    color: c.$negro;
                }
            }
        }
    }
}