@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.truck {
    margin-top: 1rem;

    .secciones {
        display: grid;
            grid-template-rows: repeat(3, 1fr);
            gap: 2rem;


        .caja {
            position: relative;
            display: inline-block;
        }
        .nove,
        .pro,
        .pru,
        .tra {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .imagen {
            
            img {
                width: 100%;
                height: 20rem;
                filter: opacity(70%);

            }
        }
    
        h3{
            //color: c.$light-blue-900;
            color: c.$deep-orange-600;
            font-size: 3rem;
        }

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 1rem;
        }
    }
    

    .contac {
        text-align: center;
        color: c.$principal;
        padding: 2rem;

        margin: 1rem;
        border: 1px solid darken(c.$gris, 10%);

    }
}